export default {
  name: 'Keyboard',
  data () {
    return {
      key: '',
      keyCode: '',
      keysVisible: false
    }
  },
  computed: {
    displayKeyInformation () {
      return window.addEventListener('keypress', (e) => {
        this.key = e.key
        this.keyCode = e.keyCode
        this.keysVisible = true
      })
    }
  }
}


